    //     console.log("quantity-select",e);
    // })


    function formatState(state) {
        if ($(state.element).parents('select').hasClass('quantity-select')) {
            let text = state.text.replace(state.id, "")
            state = $(
                `<span><b>${state.id}</b>${text}</span>`
            )
        } else {

            state = $(
                `<span>${state.text}</span>`
            )
        }
        return state;
    }

