    // product-image-preview


    // product thumb slider
    if ($(".product-thumbnail-swiper").length) {
        var product_thumbnai_swiper = new Swiper(".product-thumbnail-swiper", {
            slidesPerView: 5,
            spaceBetween: 5,
            watchSlidesProgress: true,
            direction: "horizontal",
            breakpoints: {
                568: {
                    slidesPerView: 7,
                },
                768: {
                    slidesPerView: 4,
                    direction: "vertical",
                },
                992: {
                    slidesPerView: 5,
                    spaceBetween: 10,
                    direction: "vertical",
                }
            },
            navigation: {
                nextEl: ".product-thumbnail-swiper .swiper-button-next",
                prevEl: ".product-thumbnail-swiper .swiper-button-prev",
            },
        });
    }
    if($(".zoom-container").length){
        var zoomContainerWidth,zoomContainerheight
        $(window).on("resize",function () {
            zoomContainerWidth = $(".zoom-container").eq(0).width()
            zoomContainerheight = $(".zoom-container").eq(0).height()
        }).resize()
    
        if(!screencheck(768)){
            $('.zoom-container').eq(0).ezPlus({
                scrollZoom: true,
                zoomWindowWidth : zoomContainerWidth,
                zoomWindowHeight : zoomContainerheight,
                borderSize: 1,
                responsive: true,
            });
        }
    }

    if ($(".product-image-swiper").length) {
        var product_image_swiper = new Swiper(".product-image-swiper", {
            slidesPerView: 1,
            direction: "horizontal",
            breakpoints: {
                768: {
                    direction: "vertical",
                }
            },
            thumbs: {
                swiper: product_thumbnai_swiper,
            },
            on:{
                slideChange: function () {
                    if(!screencheck(768)){
                        var activeSlide = this.slides[this.activeIndex];
                        var $img = $(activeSlide);
                        if($(".zoom-container").length){
                            $('.zoom-container').each(function () {
                                var ezPlusApi = $(this).data('ezPlus');
                                if (ezPlusApi) {
                                    ezPlusApi.destroy();
                                }
                            });

                            $img.ezPlus({
                                scrollZoom: true,
                                zoomWindowWidth : zoomContainerWidth,
                                zoomWindowHeight : zoomContainerheight,
                                borderSize: 1,
                                responsive: true,
                            }); 
                        }
                    }
                }
            }
        });
    }
    






    /* Tab Content box  ---------------------------------------------------------------------*/
    var tabBlockElement = $('.tab-data');
    $(tabBlockElement).each(function () {
        var $this = $(this),
            tabTrigger = $this.find(".tabnav li"),
            tabContent = $this.find(".tabcontent");
        var textval = [];
        tabTrigger.each(function () {
            textval.push($(this).text());
        });
        $this.find(tabTrigger).first().addClass("active");
        $this.find(tabContent).first().show();

        $(tabTrigger).on('click', function () {
            $(tabTrigger).removeClass("active");
            $(this).addClass("active");
            $(tabContent).hide().removeClass('visible');
            var activeTab = $(this).find("a").attr("data-rel");
            $this.find('#' + activeTab).fadeIn('normal').addClass('visible');

            return false;
        });

        var responsivetabActive = function () {
            if (screencheck(768)) {
                if (!$this.find('.tabMobiletrigger').length) {
                    $(tabContent).each(function (index) {
                        $(this).wrap("<div class='accordion'></div>")
                        $(this).before("<h5 class='tabMobiletrigger'>" + textval[index] + "</h5>");
                        $this.find('.tabMobiletrigger:first').addClass("rotate");
                    });
                    $('.tabMobiletrigger').click('click', function () {
                        var tabAcoordianData = $(this).next('.tabcontent');
                        if ($(tabAcoordianData).is(':visible')) {
                            $(this).removeClass('rotate');
                            $(tabAcoordianData).slideUp('normal');
                            //return false;
                        } else {
                            $this.find('.tabMobiletrigger').removeClass('rotate');
                            $(tabContent).slideUp('normal');
                            $(this).addClass('rotate');
                            $(tabAcoordianData).not(':animated').slideToggle('normal');
                        }
                        return false;
                    });
                }

            } else {
                if ($('.tabMobiletrigger').length) {
                    $('.tabMobiletrigger').remove();
                    tabTrigger.removeClass("active");
                    $this.find(tabTrigger).removeClass("active").first().addClass('active');
                    $this.find(tabContent).hide().first().show();
                }
            }
        };
        $(window).on('resize', function () {
            if (!$this.hasClass('only-tab')) {
                responsivetabActive();
            }
        }).resize();
    });


    /* Accordion box JS
    ---------------------------------------------------------------------*/
    $('.accordion-databox').each(function () {
        var $accordion = $(this),
            $accordionTrigger = $accordion.find('.accordion-trigger'),
            $accordionDatabox = $accordion.find('.accordion-data');

        $accordionTrigger.first().addClass('open');
        $accordionDatabox.first().show();

        $accordionTrigger.on('click', function (e) {
            var $this = $(this);
            var $accordionData = $this.next('.accordion-data');
            if ($accordionData.is($accordionDatabox) && $accordionData.is(':visible')) {
                $this.removeClass('open');
                $accordionData.slideUp(400);
                e.preventDefault();
            } else {
                $accordionTrigger.removeClass('open');
                $this.addClass('open');
                $accordionDatabox.slideUp(400);
                $accordionData.slideDown(400);
            }
        });
    });

    // tabnav line navgation

    if ($(".tabnav").length) {
        function tabNavLine(elem) {
            let tab = !elem.type ? $(elem) : $(elem.currentTarget);
            let width = $(tab)[0].offsetWidth;
            let left = $(tab)[0].offsetLeft;
            $(tab).parents(".tabnav").css({ "--width": width + "px", "--left": left + "px" })
        } tabNavLine(".tabnav li a")
        $(".tabnav li a").on("click", tabNavLine)
    }

