    // sidebar trigger js
    $("[data-trigger='sidebar']").on("click", function (e) {
        e.preventDefault();

        const sidebar = $("[data-side-bar]");

        if (!sidebar.hasClass("opened")) { // Check if sidebar is not already opened
            sidebar.fadeIn(200, function () {
                sidebar.addClass("opened");
            });
        }
    });

    $("[data-close-sidebar]").on("click", function (e) {
        e.preventDefault();

        const sidebar = $(this).closest("[data-side-bar]");
        sidebar.removeClass("opened");

        setTimeout(() => {
            sidebar.fadeOut(200);
        }, 200);
    });

    $(document).on("click", function (e) {
        const sidebar = $("[data-side-bar]");

        // Check if the clicked element is outside the sidebar and if the sidebar is opened
        if (!$(e.target).closest(".sidebar").length && sidebar.hasClass("opened")) {
            sidebar.removeClass("opened");
            sidebar.fadeOut(200);
        }
    });

